import React, { Component } from 'react';
import { observer, inject } from 'mobx-react';
import { Route, Switch, Link } from 'react-router-dom';

// SCSS
import './App.scss';

// basic
import Header from './Header/Header';

// pages
import Notice from './pages/Notice';
import Player from './pages/Player';
import Image from './pages/Image';
import Video from './pages/Video';
import League from './pages/League';


@inject('adminStore')
@observer
class App extends Component {
	render() {
		const { adminStore } = this.props;
		const {
			page,
			handlePage,
		} = adminStore;
		
		console.log(`현재 페이지 : ${page}`);
		
		return (
      		<div className="App">
				<Header page = {page} handlePage = {handlePage} />
				
				<Switch>
					<Route path = "/allsports/5" component = {League} />
					<Route path = "/allsports/4" component = {Video} />
					<Route path = "/allsports/3" component = {Image} />
					<Route path = "/allsports/2" component = {Player} />
					<Route path = "/allsports/1" component = {Notice}/>
				</Switch>
      		</div>
		);
	}
}

export default observer(App);
